/** @jsx jsx */
import { jsx, Container, Box } from "theme-ui"
import { graphql } from "gatsby"
import Layout from "../components/Layout"
import SEO from "../components/SEO"
import BgImage from "../components/BgImage"
import { createLocalLink } from "../utils"

const SinglePOST = ({ data }) => {
  const { content, featuredImage, title } = data.wpPost

  return (
    <Layout>
      <SEO title={title} />

      <Container sx={{ maxWidth: `l` }}>
        <BgImage img={featuredImage} height="400px" />
        <Box sx={{ variant: `card.default` }}>
          <h1 dangerouslySetInnerHTML={{ __html: title }} />
          <Box
            className="content"
            dangerouslySetInnerHTML={{ __html: createLocalLink(content) }}
          />
        </Box>
      </Container>
    </Layout>
  )
}
export default SinglePOST

export const pageQuery = graphql`
  query GET_POST($uri: String!) {
    wpPost(uri: { eq: $uri }) {
      title
      content
      featuredImage {
        ...GatsbyImageQuery
      }
    }
  }
`
